import TabBar from "./TabBar";
import {getAllEmployee} from "../../server/employee";

import Mescroll from "mescroll.js";
import "mescroll.js/mescroll.min.css";
import MescrollVue from "mescroll.js/mescroll";

export default {
  name: "employee",
  components: {TabBar, MescrollVue},
  data() {
    return {
      dataList: [],
      value: "",
      count: "0",
      loading: true,
      imgUrl: require("@/assets/img/Group.png"),

      page: {
        num: 1,
        size: 30,
      },
      mescroll: null, // mescroll实例对象
      mescrollUp: { // 上拉加载的配置.
        callback: this.getList,
        // htmlLoading: '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>',
        auto: true,
        // use:false,
        htmlNodata: '<p class="upwarp-nodata">暂无数据</p>',
        noMoreSize: 5,
        page: {
          num: 0,
          size: 30,
          total: 0
        },
        empty: {
          warpId: "main",
          icon: "./static/mescroll/mescroll-empty.png",
          tip: "暂无相关数据~" //提示
        },

      },
      imgCut: require("@/assets/img/cutOff.png")
    };
  },
  created() {
    // this.getList();
  },
  methods: {
    mescrollInit(mescroll) {
      this.mescroll = mescroll
    },
    getList(page, isSearch) {
      const that = this;
      getAllEmployee({
        status: 1,
        page: page.num,
        limit: page.size,
        searchStr: this.value
      }).then(e => {
        let {message, code, data: {list}} = e.data;
        if (code === 200) {
          if (!isSearch||page.num===1) {
            that.dataList = list;
          } else {
            that.dataList = [...that.dataList, ...list];
          }
        }
        this.$nextTick(() => {
          this.mescroll.endSuccess(list.length)
        })
      });
    },
    search: function () {
      this.getList(this.page, false);
    },
    handlePush() {
      this.$router.push({path: "/AddEmployee"});
    }
  }
};



